<template>
  <div class="stepfirst">
    <el-upload
      class="avatar-uploader"
      action="https://jsonplaceholder.typicode.com/posts/"
      ref="upload"
      :show-file-list="false"
      :auto-upload="false"
      :on-change="getFile"
    >
      <img src="outhor.png" class="avatar" fit="fill" />
    </el-upload>
  </div>
</template>

<script>
export default {
  data: function () {
    return {};
  },
  methods: {
    getFile(file) {
      //清楚filelist
      this.$refs.upload.clearFiles();
      //获取文件后缀
      let fileType = file.name.substr(file.name.lastIndexOf(".") + 1);
      
      //判断后缀是否合法
      if (
        fileType === "xlsx" ||
        fileType === "xls" ||
        fileType === "XLSX" ||
        fileType === "XLS"
      ) {
        let excelfile = new FormData()
        excelfile.append('file',file.raw)
        this.$message.success("ok");
        var reader = new FileReader();
        reader.readAsBinaryString(file);
        // console.log(reader)
      } else {
        this.$message.error("上传只能是 excel 格式!");
      }
    },
  },
};
</script>

<style scoped>
.stepfirst {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.stepfirst >>> .avatar {
  width: 200px;
  height: 200px;
}
</style>